import React, { Component } from 'react';
import { utf8ToText, textToUtf8 } from '../../../lib/utf8';

export default class UTF8Convert extends Component {
    render() {
        return (
            // <form>
                <div className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">UTF8 Convert</h3>
                    </div>
                    <div className="box-body">
                        <div className="form-group">
                            <label>Text</label>
                            <textarea
                                onChange={(event) => { this.encoded = textToUtf8(event.target.value); this.decoded = event.target.value; this.forceUpdate() }}
                                className="form-control"
                                rows="3"
                                placeholder="TEXT"
                                style={{ "margin": "0px 3px 0px 0px", "width": "671px", "height": "291px" }}
                                value={this.decoded || ''}>
                            </textarea>
                        </div>
                        <div className="form-group">
                            <label>UTF8</label>
                            <textarea
                                onChange={(event) => { this.encoded = event.target.value; this.decoded = utf8ToText(event.target.value); this.forceUpdate() }}
                                className="form-control"
                                rows="3"
                                placeholder="bytes"
                                style={{ "margin": "0px 3px 0px 0px", "width": "671px", "height": "291px" }}
                                value={this.encoded || ''}>
                            </textarea>
                        </div>
                    </div>
                </div>
            // </form>
        )
    }
}
export { UTF8Convert };